  .lambda{
    text-align: center;
  }
  .HomePage {
    text-align: center;
  }
  
  .Knuckles-img{
    border-radius: 50%;
  }
  
  .App-logo {
    height: 20vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
    .Knuckles {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #eee;
    min-height: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .xd{
    font-weight: 1000;
    font-style: italic;
    color: forestgreen;
  }